import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Rx';

import { CaptchaComponent } from '../angular-captcha';

import { BasicService } from './basic.service';

@Component({
  moduleId: module.id,
  selector: 'basic-form',
  templateUrl: 'basic.component.html',
  styleUrls: ['basic.component.css'],
  providers: [BasicService]
})
export class BasicComponent {

  /**
   * Captcha validation messages.
   */
  errorMessages: string;

  /**
   * BotDetect CAPTCHA component.
   */
  @ViewChild(CaptchaComponent) captchaComponent: CaptchaComponent;

  constructor(
    private basicService: BasicService,
    private router: Router  
  ) { }

  /**
   * Validate captcha at server-side.
   */
  validate(value, valid): void {

    // the user-entered captcha code value to be validated at the backend side
    let userEnteredCaptchaCode = this.captchaComponent.userEnteredCaptchaCode;
    
    // the id of a captcha instance that the user tried to solve
    let captchaId = this.captchaComponent.captchaId;

    const postData = {
      userEnteredCaptchaCode: userEnteredCaptchaCode,
      captchaId: captchaId
    };
  
    this.basicService.send(postData)
      .subscribe(
        response => {
          if (response.success == false) {
            // captcha validation failed; reload image
            this.errorMessages = 'CAPTCHA validation failed.';
            this.captchaComponent.reloadImage();
          } else {
            // captcha validation succeeded; proceed with your workflow
            this.router.navigate(['/basic-success-notify']) 
          }
        },
        error => {
          throw new Error(error);
        });
  }

}



